import styled from "styled-components";

export const H1 = styled.h1`
  font-weight: bold;
  font-size: 60px;

  @media (max-width: 914px) {
    font-size: 50px;
  }

  @media (max-width: 768px) {
    font-size: 40px;
  }
`;
export const H2 = styled.h2`
  font-weight: 600;
  font-size: 45px;

  @media (max-width: 914px) {
    font-size: 40px;
  }

  @media (max-width: 768px) {
    font-size: 35px;
  }
`;
export const H3 = styled.h3`
  font-weight: 500;
  font-size: 30px;

  @media (max-width: 450px) {
    font-size: 24px;
  }
`;
export const H4 = styled.h4`
  font-weight: 500;
  font-size: 24px;

  @media (max-width: 450px) {
    font-size: 18px;
  }
`;
export const BodyIntro = styled.p`
  font-weight: 500;
  font-size: 24px;
  line-height: 140%;
`;
export const BodyMain = styled.p`
  font-weight: normal;
  font-size: 20px;
  line-height: 140%;
`;
export const MediumText = styled.p`
  font-weight: normal;
  font-size: 18px;
  line-height: 3rem;
`;
export const Caption = styled.p`
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
`;
export const Caption2 = styled.p`
  font-weight: 600;
  font-size: 15px;
  line-height: 18px;
  text-transform: uppercase;
`;
export const SmallText = styled.p`
  font-weight: normal;
  font-size: 13px;
  line-height: 130%;
`;
export const SmallText2 = styled.p`
  font-weight: 600;
  font-size: 13px;
  line-height: 130%;
  text-transform: uppercase;
`;
export const BoldText = styled.p`
  font-weight: 600;
  font-size: 18px;
  line-height: 3rem;
`;
